import React, { useState, useEffect, useContext } from "react";
import { ProductComponent } from "../Components";
import { getDatabase, child, get, ref } from "firebase/database";
import { app } from "../Database";
import "./newProduct.scss";
import { Context } from "../context";

function NewProduct({ collectionId = 3, data, amount }) {
    let number = [];

    return (
        <div className="newProduct">
            <div
                className="newProduct__inner"
                style={{ gridTemplateColumns: `repeat(${amount}, 1fr)` }}
            >
                {Object.keys(data).map((id, ind) => {
                    if (id == collectionId - 1) {
                        return data[id].collectionProducts.map(
                            (item, index) => {
                                if (item.newProduct) {
                                    number.push(item);
                                    return (
                                        <ProductComponent
                                            data={data[id].collectionProducts}
                                            id={index}
                                            collectionId={data[id].collectionId}
                                            key={item + index}
                                        />
                                    );
                                }
                            }
                        );
                    }
                })}
            </div>
        </div>
    );
}

export default NewProduct;
